/* Contents */

// Auth
.authbox, {

  max-width: 780px;
  margin: 0 auto 10rem;
  padding: 80px;
  border: 1px solid $border;
  border-radius: 32px;
  &-title {
    margin-bottom: 54px;
    h1 {
      margin-bottom: 24px; }
    p {
      color: $secn-text; } } }


.ehealth {
  padding-bottom: 80px;
  &-title {
    text-align: center;
    margin-bottom: 80px; }
  &-box {
    padding: 56px 136px;
    border: 1px solid $border;
    max-width: 780px;
    margin: 0 auto;
    border-radius: 32px; } }


// Homepage
.homepage {
  padding-bottom: 40px;
  h1 {
    font-size: 3.111rem;
    line-height: 1.2em;
    span {
      font-size: 1.333rem;
      line-height: 1.3em; } }
  &-masthead {
    margin-bottom: 40px;
    .slider {
      &-item {
        width: 100%;
        padding: 19.608% 0;
        position: relative;
        border-radius: 32px;
        overflow: hidden;
        img {
          position: absolute;
          top: 0;
          left: 0;
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center; } }
      &-text {
        position: absolute;
        top: 0;
        left: 0;
        padding: 5rem;
        max-width: 680px;
        h2 {
          font-size: 3.111rem;
          line-height: 1.2em; } } } } }


.newsupdate,
.allproducts {
  padding-bottom: 40px;
  &-detail {
    .meta {
      display: flex;
      margin-bottom: 40px;
      &-item {
        color: #A7A7A7;
        &:not(:last-child) {
          margin-right: 24px; }
        .ic {
          margin-right: 8px; } } }
    &_content {
      .masthead_image {
        margin-bottom: 40px;
        img {
          display: block; } }
      article {
        margin-bottom: 64px; }
      .share-article {
        padding: 38px 42px;
        border-radius: 8px;
        border: 1px solid #DBDBDB;
        text-align: right;
        margin-bottom: 40px; } } } }

// Product Single
.product-single {
  padding-bottom: 80px;
  &_img {
    .big-img {
      position: relative;
      width: 100%;
      padding: 33% 0;
      border-radius: 8px;
      overflow: hidden;
      margin-bottom: 20px;
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center; } }
    .product-thumbnail {
      width: 100%;
      max-width: 640px;
      margin: 0 auto;
      .item {
        width: 138px;
        margin: 0 auto;
        // height: 138px
        .thumbnail {
          width: 100%;
          padding: 50% 0;
          border-radius: 8px;
          position: relative;
          overflow: hidden;
          img {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            object-fit: cover;
            object-position: center; } } } } }

  .detailTOp {
    .col-md-6 {
      &:last-child {
        display: flex;
        justify-content: flex-end; } } }

  &_moredetail {
    padding-top: 40px; } }

.product-details {
  max-width: 680px;
  h2 {
    font-weight: 400;
    margin-bottom: 8px; }
  .price {
    padding-bottom: 20px;
    margin-bottom: 20px;
    border-bottom: 1px solid #A7A7A7;
    b {
      font-weight: 700;
      font-size: 36px;
      line-height: 54px; } }
  .description {
    color: $secn-text;
    margin-bottom: 100px;
    strong {
      color: $text;
      font-size: 18px;
      line-height: 27px;
      font-weight: 500;
      display: block;
      margin-bottom: 20px; } } }

.list-review {
  .review-item {
    &:not(:last-child) {
      margin-bottom: 32px; }
    &_top {
      position: relative;
      display: flex;
      align-items: center;
      margin-bottom: 28px;
      .photo {
        width: 56px;
        padding: 1.78% 0;
        position: relative;
        border-radius: 4px;
        overflow: hidden;
        margin-right: 16px;
        img {
          width: 100%;
          height: 100%;
          object-fit: cover;
          object-position: center;
          position: absolute;
          top: 0;
          left: 0; } }
      .name {
        font-size: 18px;
        line-height: 27px;
        font-size: 500; } } } }


// Elearning
.elearing {
  &-content {
    &_pdf,
    &_video {
      padding-bottom: 40px;
      margin-bottom: 40px;
      .pdfwrap {
        height: 900px; } }
    &_video {} } }

// Notif Page
.notif-page {
  padding-bottom: 80px;
  .nav-tabs {
    border-bottom: 0;
    justify-content: center;
    margin-bottom: 80px;
    .nav-item {
      &:not(:last-child) {
        margin-right: 40px; }
      .nav-link {
        border: 0;
        color: #000;
        opacity: 0.5;
        padding: 0;
        &.active {
          font-weight: 600;
          color: $primary;
          opacity: 1;
          border-bottom: 1px solid $primary; }
        &:hover {
          color: $primary; } } } }

  .notif-item {
    display: block;
    color: $text;
    text-decoration: none;
    &:not(:last-child) {
      margin-bottom: 32px; }
    &:hover {
      .notif-item_title {
        b {
          color: $primary; } } }
    &_title {
      display: flex;
      align-items: center;
      margin-bottom: 24px;
      b {
        font-weight: 500; }
      .icon-med {
        margin-right: 16px; } } } }

// Account
.account {
  padding-bottom: 80px;
  &-wrap {
    padding: 64px;
    border: 1px solid #DBDBDB;
    border-radius: 32px; }
  &-profile {
    &_top {
      margin-bottom: 40px;
      h4 {
        font-size: 36px;
        line-height:  42px;
        margin-bottom: 0; }
      .col-6 {
        &:last-child {
          display: flex;
          justify-content: flex-end; } } }
    &_photo {
      width: 150px;
      padding: 16.095% 0;
      position: relative;
      border-radius: 100px;
      overflow: hidden;
      img {
        width: 100%;
        height: 100%;
        position: absolute;
        top: 0;
        left: 0;
        object-fit: cover;
        object-position: center; } } } }

// Cart
.product-cart {
  padding-bottom: 80px;
  &_top {
    text-align: center;
    margin-bottom: 80px;
    h1 {
      font-size: 48px;
      line-height: 58px;
      margin-bottom: 40px; }
    .cart-progress {
      display: flex;
      justify-content: center;
      .progress-item {
        .number {
          display: inline-block;
          min-width: 56px;
          line-height: 54px;
          border-radius: 50px;
          text-align: center;
          border: 1px solid #DBDBDB;
          margin-right: 24px;
          color: #DBDBDB; }
        .text {
          color: #DBDBDB; }
        &.active {
          .number {
            border-color: $primary;
            color: $primary; }
          .text {
            color: $primary; } }
        & + .progress-item {
          margin-left: 430px;
          position: relative;
          &:before {
            content: "";
            width: 300px;
            height: 1px;
            background: #000;
            position: absolute;
            top: calc(50% - 1px);
            right: calc(100% + 64px); } } } } } }

.checkout {
  &-summary {
    padding: 32px 24px;
    border: 1px solid rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    h4 {
      text-align: center;
      margin-bottom: 48px; }
    &_list {
      margin-bottom: 40px;
      .item {
        display: grid;
        grid-template-columns: 50px 2fr 1fr;
        &:not(:last-child) {
          margin-bottom: 24px; }
        .nominal {
          text-align: right; }
        & > div {
          padding: 0 8px; } } }

    &_total {
      display: flex;
      justify-content: space-between;
      padding-bottom: 32px;
      margin-bottom: 40px;
      border-bottom: 1px solid rgba(0, 0, 0, 0.5);
      b,
      span {
        font-size: 24px;
        line-height: 36px;
        font-weight: 600; }
      span {
        color: $primary; } }
    &_payment {
      .payment-label {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 32px;
        h4 {
          margin-bottom: 0; } }
      .btn-group-toggle {
        margin-left: -8px;
        margin-right: -8px;
        display: flex;
        align-items: center;
        margin-bottom: 48px;
        .btn {
          margin-left: 8px;
          margin-right: 8px;
          border-radius: 8px !important;
          transition: all .3s ease-in-out;
          &.active {
            background: $text;
            color: #FFF; } } } } } }



