/* Header */
.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  transition: all .5s ease-in-out;
  z-index: 999;
  background: #FFF;
  .container {
    padding-top: 30px;
    padding-bottom: 30px;
    border-bottom: 1px solid $border;
    transition: all .5s ease-in-out;
    .row {
      align-items: center; } }
  &-logo {
    max-height: 60px;
    transition: all .5s ease-in-out;
    img {
      width: auto;
      max-height: 60px;
      object-fit: contain;
      object-position: center;
      transition: all .3s ease-in-out; } }
  &-right {
    display: flex;
    justify-content: flex-end; }
  &-menu {
    position: relative;
    margin-right: 50px;
    &:after {
      content: "";
      display: block;
      width: 1px;
      height: 32px;
      background: $text;
      position: absolute;
      top: -8px;
      right: 0; }
    &_item {
      color: $text;
      transition: all .3s ease-in-out;
      padding-right: 48px;
      text-decoration: none;
      &:hover {
        color: $primary; } } }
  &-icon {
    .icon {
      color: $text;
      &:not(:last-child) {
        margin-right: 32px; }
      &:hover {
        color: $primary; } } }
  &.sticky {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    border-bottom: 1px solid #DBDBDB;
    .container {
      padding-top: 15px;
      padding-bottom: 15px;
      border-bottom: 0; }
    .header-logo {
      img {
        max-height: 30px; } } } }

